import { Link } from 'gatsby'
import React from 'react'
import Helmet from 'react-helmet'
import { Waypoint } from 'react-waypoint'
import pic01 from '../assets/images/pic01.jpg'
import Header from '../components/Header'
import Layout from '../components/layout'
import Nav from '../components/Nav'
import axios from 'axios';
class Index extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      stickyNav: false,
      items:{naemi:[],prodajbi:[]}
    }
  }

  _handleWaypointEnter = () => {
    this.setState(() => ({ stickyNav: false }))
  }

  _handleWaypointLeave = () => {
    this.setState(() => ({ stickyNav: true }))
  }
componentDidMount() {
  axios.get(`https://rpibupc724.execute-api.us-east-1.amazonaws.com/dev`)
      .then(res => {
        const items = res.data;
        console.log(items);
        this.setState({ items });
      })
}

  render() {
    return (
      <Layout>
        <Helmet title="    / " google-site-verification="E3ToEEmpO7q1RNns6WDtxLIxuAPDHGEvqSuQi92fYWI" />

        <Header />
 
        <Waypoint
          onEnter={this._handleWaypointEnter}
          onLeave={this._handleWaypointLeave}
        ></Waypoint>
        <Nav sticky={this.state.stickyNav} />

        <div id="main">
          <section id="intro" className="main">
            <div className="spotlight">
              <div className="content">
                <header className="major">
                  <h2> </h2>
                </header>
                <p>
                        "" . .             - ,   .           .     .        ,    .          .       .
                </p>
                 
              </div>
              <span className="image">
                <img src={pic01} alt="" />
              </span>
            </div>
          </section>

          <section id="first" className="main special">
            <header className="major">
              <h2></h2>
            </header>
            <ul className="features">
              {this.state.items.naemi.map(item => (<li key={item.id}>
                <a href={item.href}>
                 <span className="image">
                <img src={item.img} alt="" style={{width:280, height:180}} />
              </span>
                <h3>{item.title}</h3>
                <p>
                  {item.description}
                  </p>
                  </a>
              </li>
               ))}
              
            </ul>
            
          </section>

          <section id="second" className="main special">
               <header className="major">
              <h2></h2>
            </header>
           <ul className="features">
              {this.state.items.prodajbi.map(item => (<li key={item.id}>
                <a href={item.href}>
                 <span className="image">
                <img src={item.img} alt="" style={{width:280, height:180}} />
              </span>
                <h3>{item.title}</h3>
                <p>
                 {item.description}
                  </p>
                  </a>
              </li>
               ))}
              
            </ul>
              
          </section>

        
        </div>
         <section id="contact" className="main special"></section>
      </Layout>
    )
  }
}

export default Index
